export default {
	data() {
		return {
			imageSrc: [0, 1, 2, 3]
		}
	},
	methods: {
		scanCode() {
			uni.scanCode({
				onlyFromCamera: true,
				scanType: ['qrCode'],
				success(res) {
					console.log(res)
				},
				fail(res) {
					console.log(res)
					uni.showToast({
						title: '扫码失败，请重试',
						icon: 'none',
						duration: 2000
					})
				}
			})
		},
		library() {
			uni.navigateTo({
				url:'library/library'
			})
		},
		scoreManger(){
			uni.navigateTo({
				url: 'scoreSystem/scoreSystem'
			})
		},
		reviewLesson(){
			uni.navigateTo({
				url: 'reviewLesson/reviewLesson'
			})
		},
		repair(){
			uni.navigateTo({
				url: 'repair/repair'
			})
		},
		cetSystem(){
			uni.navigateTo({
				url : 'cetQuery/cetQuery'
			})
		}
	}
